let _this = this;
export default {
	
	data() {
		return {
			br:'\n',
			title:'',
			datetime:'',
			address:'',
			ask:'',
			startDatetime:'',
			
			
		}
	},
	methods: {
		
	},
	onLoad: function(option){
		console.log('option:');
		console.log(option.apid);
		
		uni.showLoading({
			title:'加载数据中'
		});
		let appointmentId = option.apid;
		let url = this.createApiUrl('get-appointment?appointmentId=' + appointmentId);
		
		uni.request({
			url: url,
			method: 'GET',
			data: {},
			success: res => {
				console.log('url')
				console.log(url);
				uni.hideLoading();
				res = res.data.data;
				console.log(res);
				
				this.title = res.title;
				console.log("this.title:" + this.title);
				this.ask = res.ask;
				this.datetime = res.date;
				this.address = res.address;
				this.startDatetime = res.startDatetime;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onShow:function(options){

		
		
	}
}
